          div(numero="1" titulo="Desinfeccin de equipos") Cuando la limpieza de los equipos se realiza sin tener que remover ninguno de sus componentes se dice que este es un proceso CIP (<i>clean-in-place</i>). Por el contrario, cuando la limpieza del equipo requiere del desmonte de alguna de sus partes, se denomina un proceso COP (<i>clean-out-of-place</i>). Generalmente, en la limpieza y desinfeccin de los equipos se utilizan detergentes con baja produccin de espuma.
