    .titulo-tercero
      h3 Detergentes y desinfectantes
    p.mb-5  Los detergentes facilitan la remocin de las partculas de suciedad, por lo que son recomendados en todos los procesos de limpieza y desinfeccin. 
    TabsC.color-acento-contenido
      .py-3.py-md-4(titulo="Detergentes")
        .row
          .col-md-7.mb-4.mb-md-0
            p.mb-3  Los detergentes facilitan la remocin de las partculas de suciedad, por lo que son recomendados en todos los procesos de limpieza y desinfeccin. Sin embargo, los detergentes de uso general son poco recomendados en superficies donde se hace procesamiento, en donde se recomienda, en su lugar, el uso de detergentes alcalinos o clorados. 
            p.mb-3  Los detergentes clorados se emplean en superficies que, en ocasiones, resultan difciles de limpiar, pero no se recomiendan en superficies como el aluminio, debido a que son altamente corrosivos. Es importante mencionar que a pesar de que los detergentes son fundamentales en los procesos de limpieza, no son desinfectantes.   
            p.mb-3  Dentro de la clasificacin de los detergentes, existen los de uso general, los alcalino o clorados, los cidos y los enzimticos y la eficacia de cualquiera de ellos depende de los siguientes factores:        
          .col-md-5
            figure
              img(src='@/assets/curso/t5-i5.jpg', alt='Texto que describa la imagen')
          .clearfix.mb-5
          .col-lg-12
            figure.slideInLeft
              img(src='@/assets/curso/t5-gx1.svg', alt='Texto que describa la imagen')            

      .py-3.py-md-4(titulo="Desinfectantes")
        .row
          .col-md-7.mb-4.mb-md-0
            p.mb-3  En cuanto a los desinfectantes se tiene que es posible que estos no requieran enjuague antes de su uso, aunque esto depende del tipo de desinfectante y su concentracin. Estos productos deben estar aprobados por la autoridad sanitaria y su empleo debe realizarse siguiendo las recomendaciones del fabricante. 
            p.mb-3  Los productos desinfectantes basados en cloro son los ms utilizados en diversas industrias, debido a que toleran aguas calcreas, son fciles de conseguir, econmicos, adems que contrarrestan una amplia variedad de hongos y bacterias. Adems del cloro, existen otros compuestos basados en amonio cuaternario, que tienen como desventaja que requieren mayor tiempo de exposicin para aumentar su efectividad, lo cual les concede un mayor efecto residual. Debido a que no son eficaces frente a algunos patgenos, es recomendable rotarlos con otros desinfectantes.
          .col-md-5
            figure.slideInLeft
              img(src='@/assets/curso/t5-i6.jpg', alt='Texto que describa la imagen')

      .py-3.py-md-4(titulo="Otros tipo de desinfectantes")
        .row
          .col-md-7.mb-4.mb-md-0
            p.mb-3  En una tercera categora, estn los desinfectantes basados en iodo, denominados iodforos, los cuales resultan altamente eficaces contra diversos patgenos, incluso hongos y levaduras incluso en bajas concentraciones. Otra de sus ventajas, es que son ms estables, menos corrosivos, no irritan la piel y funcionan bien con aguas calcreas. 
            p.mb-3  Otro grupo lo conforman los desinfectantes cidos. Estos poseen una estructura ms estable a altas temperaturas o en presencia de materia orgnica, por lo que se utilizan especialmente en la limpieza de equipos. Finalmente, existen otros agentes desinfectantes entre los cuales se destacan el ozono, la luz ultravioleta y el agua caliente, pero su uso es menos comn.
          .col-md-5
            figure.slideInLeft
