            p.text-center Mantener la bodega en buenas condiciones de ventilacin, humedad e iluminacin.

    p.mb-5  Como en todas las actividades humanas, las explotaciones pecuarias tambin son generadoras de residuos de toda ndole, y cuando no reciben un manejo apropiado terminan convirtindose en foco de contaminacin y de proliferacin de especies indeseadas.
    .tarjeta.bg-sliders.p-5.mb-5.shadow-box 
