        .col-lg-6
          .h4 Manejo seguro de residuos
          p.mb-3  Para lograr una eficiente gestin de los residuos slidos, es necesario, en primer lugar, diferenciarlos unos de otros, de acuerdo con los parmetros de clasificacin existentes. En segunda instancia, es imperativo definir el sistema o los sistemas a emplear para hacer una gestin integral de los residuos, de manera que los impactos que estos puedan generar no trasciendan de la unidad productiva.

        .col-lg-5
