    .titulo-tercero
      h3  Disposicin final
    .row.mb-5
      .col-lg-7
        p.mb-3  La actividad productiva pecuaria, al igual que otras explotaciones, genera una gran cantidad de residuos, la mayora de ellos orgnicos, los cuales deben manejarse adecuadamente para evitar la proliferacin de patgenos, la contaminacin de suelos y aguas y la generacin de olores ofensivos. La disposicin final de los residuos se realiza de acuerdo con el tipo de material que se genere, conforme a la clasificacin establecida en la explotacin pecuaria. 
        p.mb-3  Respecto a los mtodos para la disposicin final de estos residuos se tiene lo siguiente:
      .col-lg-5 
        figure(data-aos='flip-left')
          img(src='@/assets/curso/t5-i10.jpg' alt='imagen decorativa')      


    .row.mb-5
      .col-lg-5
        figure(data-aos='slide-left')
          img(src='@/assets/curso/t5-i11.jpg' alt='imagen decorativa')              
      .col-lg-7
        AcordionA.mb-5(tipo="a" clase-tarjeta="tarjeta tarjeta--gris")
          .div(titulo="Residuos reciclables")
            p.mb-3  Estos se depositan en recipientes ubicados dentro de la explotacin pecuaria los cuales deben estar debidamente rotulados y dispuestos en un rea especfica. El sitio de almacenamiento de los residuos debe estar bien ventilado, tener fcil acceso, estar construido con materiales que permitan la limpieza de sus superficies y que disponga de los medios para evitar la entrada de vectores (Decreto 1140 de 2003).
          .div(titulo="Residuos no reciclables")
            p.mb-3   Estos materiales tambin se almacenan en contenedores debidamente rotulados, los cuales deben indicar claramente qu tipo de materiales se pueden depositar all. Estos materiales se almacenarn en un lugar destinado para tal fin, el cual debe permitir su fcil limpieza, tener buen acceso y restringir el acceso de vectores.
          .div(titulo="Residuos orgnicos")
            p.mb-3  Este tipo de residuos pueden recibir un manejo especial al interior de la explotacin pecuaria, pues son totalmente biodegradables y pueden transformarse en abono fcilmente. La gestin de estos residuos comienza con la separacin en la fuente. Los residuos orgnicos se disponen en reas de compostaje o lombricultivo en las cuales mediante procesos enteramente naturales se degradan. Es importante capacitar al personal sobre la importancia de separar los residuos y hacer una correcta disposicin.  
          .div(titulo="Residuos especiales")
            p.mb-3 Este tipo de residuos requieren de un manejo distinto debido a que, por su naturaleza y los componentes que los integran, resultan peligrosos para los animales y para el personal que labora en la unidad productiva. Los envases de plaguicidas deben someterse en primera instancia a un proceso conocido como triple lavado, el cual consiste en enjuagar con agua tres veces el recipiente antes de perforarlo y almacenarlo. En relacin con los residuos de vacunas se inactivan sumergindolos en soluciones desinfectantes por 24 horas. Por su parte las agujas empleadas para la inyeccin de medicamentos se disponen en guardianes, para su posterior disposicin.
            p.mb-3  En algunas explotaciones avcolas el manejo de las mortalidades es uno de los temas ms complejos, por lo que en muchas de ellas se opta por enterrar los animales y as se reducen los riesgos de transmisin de enfermedades y olores ofensivos.

















